import {
	ajax
} from './request.js'

// 
export const userInfo = params => {
	return ajax(params, '/user/info', 'GET')
}

// 
export const updateUser = params => {
	return ajax(params, '/user/updateUser', 'POST')
}

// 
export const storageUpload = params => {
	return ajax(params, '/storage/upload', 'GET')
}


